header{
    max-width: 1110px;
    div{
        width: 17%;
    }
    h1{
        width: 17%;
    }
    nav{
        width: 17%;
        ul{
            height: 100%;
        }
    }
}

.banner{
    width: 100%;
    height: 100%;
    background: url(https://images.unsplash.com/photo-1506846540181-347008948fff?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=593&q=80) no-repeat;
    background-position: center center;
    background-size: cover;
    padding: 167px 772px 167px 180px;
    button{
      width: 160px;
      height: 45px;
    }
  }

  .menu{
    ul{
      max-width: 730px;
    }
  }

  .like-guess{
    max-width: 1110px;
    .like-guess-exhibition{
      ul{
        li{
          img{
            height: 320px;
            width: 320px;
          }
        }
      }
    }
  }

  .sencond-banner{
    width: 100%;
    height: 100%;
    background: url(https://images.unsplash.com/photo-1536768767433-cc552f38def9?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=750&q=80);
    background-position: center center;
    background-size: cover;
    padding: 208px 750px 36px 85px;
    .banner-content{
      width: 445px;
      height: 96px;
      line-height: 1.5;
    }
    .subscribe{
      input{
        height: 48px;
      }
      input[type="text"]{
        padding: 12px 64px 12px 12px;
      }
      input[type="button"]{
        padding: 12px 52px;
      }
    }
  }

.footer{
    max-width: 1110px;
    .footer-topic{
        width: 36.8%;
    }
    nav{
        width: 11%;
        ul{
            width: 100%;
            height: 100%;
        }
    }
}
